import Vue from "vue";

Vue.prototype.init_traffic = function () {
    var url_vars = this.getUrlVars();
    var keys = Object.keys(url_vars);
    console.log("[init_traffic]:", keys);
    for (let i = 0; i < keys.length; ++i) {
    }

    var source_from = undefined;
    var source_from_keys = ['f', 'source_from'];
    if (url_vars.f) {
        localStorage.setItem('f', url_vars.f);
    } else {
        var f = localStorage.getItem('f');
        if (!f) {
            f = document.referrer;
            localStorage.setItem('f', f);
        }
    }
}

Vue.prototype.get_from_source = function () {
    var f = localStorage.getItem('f');
    return f;
}
